@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@500&display=swap');

body {

    font-family: 'Roboto', sans-serif;
}

.app {

    padding-top: 5rem;
    display: flex;
    justify-content: center;
}

.wrap {

    width: 500px;
    
}

$height: 46px;

.select {
    width: 100%;
    position: relative;
    z-index: 100;


    &.open {

        .select__drop {
            display: block;
        }

        .select__input {

            border-bottom: none ;

        }

        .select__backdrop {
            display: block;
        }

    }

    &__input {
        border: 1px solid #d8d1d1;
        border-radius: 5px;
        height: $height;
        display: flex ;
        align-items: center ;
        justify-content: space-between;
        padding: 0 1rem;
        cursor: pointer;
    }

    &__drop {
        display: none;
        position: absolute;
        border: 1px solid #cccccc;
        border-radius: 5px;
        top: $height;
        left: 0;
        right: 0;
        max-height: 200px;
        overflow-y: auto;
        box-shadow: 1px 2px 4px rgba(0,0,0,0.2);
    }

    &__list {
        padding: 0;
        margin: 0;
        list-style: none;
    }

    &__backdrop {
        position: fixed;
        display: none;
        z-index: -1;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background: transparent;
    }

    &__item {
        padding: 1rem;
        border-bottom: 1px solid #cccccc;

        &.selected{
            background-color: #dcf3fd;
        }

        &:hover {
            background-color: #eee;
            cursor: pointer;
            transition: .15ms background-color ease-in;
        }
    }

}